import React from "react";
import { Link  } from "react-router-dom";
import { Button } from '@material-ui/core';

function Menubar() {
    return(
        <>
            <Button>
                <Link to = "/">main</Link>
            </Button>
            <Button>
                <Link to = "/fac1">  1</Link>
            </Button>
            <Button>
                <Link to = "/fac2">  2</Link>
            </Button>
            <Button>
                <Link to = "/fac3">  3</Link>
            </Button>
            <Button>
                <Link to = "/fac4">  4</Link>
            </Button>
            <Button>
                <Link to = "/fac5">  5</Link>
            </Button>
            <Button>
                <Link to = "/fac6">  6</Link>
            </Button>
        </>
    );
}

export default Menubar;