import {
    Table, TableBody, TableCell, TableContainer, TableHead, TableRow,
    Paper, Button, ButtonGroup
  } from '@material-ui/core';
import Mainchart from '../../src/components/mainchart'

const stt = ['', ' ', ' ']
const factory = Array(6)    //  
    .fill()
    .map(() => ({
    'id' : Math.floor(Math.random()*10),
    'name' : '',
    'state' : stt[(Math.floor(Math.random()*10/3))],
    'process1' : Math.floor(Math.random()*100),
    'process2' : Math.floor(Math.random()*100),
    'process3' : Math.floor(Math.random()*100),
    'process4' : Math.floor(Math.random()*100),
    'process5' : Math.floor(Math.random()*100),
    'process6' : Math.floor(Math.random()*100),
    'pass' : Math.floor(Math.random()*1000),
    'nonpass' : Math.floor(Math.random()*10)
}));

const Main = () => {
    return (
      <div>
        <div className="TextBox">
        [      ]
      </div>
      <div className="MainTable">
        {
          <TableContainer component={Paper}>
            <Table size="small">
                <TableHead>
                    <TableRow>
                        <TableCell align="left"></TableCell>
                        <TableCell align="middle"></TableCell>
                        <TableCell align="middle">1 </TableCell>
                        <TableCell align="middle">2 </TableCell>
                        <TableCell align="middle">3 </TableCell>
                        <TableCell align="middle">4 </TableCell>
                        <TableCell align="middle">5 </TableCell>
                        <TableCell align="middle">6 </TableCell>
                        <TableCell align="middle"> </TableCell>
                        <TableCell align="middle"></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                  {factory.map(({id, name, state, process1, process2, process3, process4, process5, process6, pass, nonpass}, i) => (
                    <TableRow key={id}>
                      <TableCell align="middle">{name + String(i + 1)}</TableCell>
                      <TableCell align="middle">{state}</TableCell>
                      <TableCell align="middle">{process1}</TableCell>
                      <TableCell align="middle">{process2}</TableCell>
                      <TableCell align="middle">{process3}</TableCell>
                      <TableCell align="middle">{process4}</TableCell>
                      <TableCell align="middle">{process5}</TableCell>
                      <TableCell align="middle">{process6}</TableCell>
                      <TableCell align="middle">{pass}</TableCell>
                      <TableCell align="middle">{nonpass + '%'}</TableCell>
                  </TableRow>
                  ))}
                </TableBody>
            </Table>
        </TableContainer>
        }
      </div>
      <div className="HorizontalLine"></div>
      <div className="TextBox">
        [  report ]
        <div className="buttonbox">
          <ButtonGroup variant="outlined" aria-label="outlined button group">
            <Button variant="outlined" color="primary">  1</Button>
            <Button variant="outlined" color="primary">  2</Button>
            <Button variant="outlined" color="primary">  3</Button>
            <Button variant="outlined" color="primary">  4</Button>
            <Button variant="outlined" color="primary">  5</Button>
            <Button variant="outlined" color="primary">  6</Button>
          </ButtonGroup>
        </div>
      </div>
      <div className="MainTable">
        {
          <TableContainer component={Paper}>
            <Table size="small">
                <TableHead>
                    <TableRow>
                        <TableCell align="left"></TableCell>
                        <TableCell align="middle"></TableCell>
                        <TableCell align="middle">1 data</TableCell>
                        <TableCell align="middle">2 data</TableCell>
                        <TableCell align="middle">3 data</TableCell>
                        <TableCell align="middle">4 data</TableCell>
                        <TableCell align="middle">5 data</TableCell>
                        <TableCell align="middle">6 data</TableCell>
                        <TableCell align="middle"> </TableCell>
                        <TableCell align="middle"> </TableCell>
                        <TableCell align="middle"></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                  {factory.map(({id, process1, process2, process3, process4, process5, process6, pass, nonpass}, i) => (
                    <TableRow key={id}>
                      <TableCell align="middle">{'2023-01-20'}</TableCell>
                      <TableCell align="middle">{'18:35'}</TableCell>
                      <TableCell align="middle">{process1}</TableCell>
                      <TableCell align="middle">{process2}</TableCell>
                      <TableCell align="middle">{process3}</TableCell>
                      <TableCell align="middle">{process4}</TableCell>
                      <TableCell align="middle">{process5}</TableCell>
                      <TableCell align="middle">{process6}</TableCell>
                      <TableCell align="middle">{pass}</TableCell>
                      <TableCell align="middle">{nonpass}</TableCell>
                      <TableCell align="middle">{98}</TableCell>
                  </TableRow>
                  ))}
                </TableBody>
            </Table>
        </TableContainer>
        }
      </div>
      <div className="margin_short" />
      <div className="MainTable"><Mainchart /></div>
      </div>
    );
  };
  
  export default Main;